<template>
  <div id="dsc1" >
    <list :year="year" :week="week" :day="1" weekc="" id="Mon" />
    <list :year="year" :week="week" :day="2" weekc="" id="Tue" />
    <list :year="year" :week="week" :day="3" weekc="" id="Wed" />
    <list :year="year" :week="week" :day="4" weekc="" id="Thurs" />
    <list :year="year" :week="week" :day="5" weekc="" id="Fri" />
    <list :year="year" :week="week" :day="6" weekc="" id="Sat" />
    <list
      :year="year"
      :week="Number(week) + 1"
      weekc=""
      day="0"
      id="Sun"
    />
    <el-date-picker
      v-model="value1"
      type="week"
      format="yyyy  WW "
      placeholder=""
      class="el1"

    />

    <!---->
    <textarea rows="18" cols="53" v-model="note" @keyup.enter.ctrl="add">
      </textarea>





    <!--//-->
  </div>
</template>

<script>
  import axios from "axios";
import List from "./schedule/List.vue";
export default {
  name: "Schedule",
  data() {
    return {
      value1: new Date(),
      note:""
    };
  },
  props: [],
  components: {
    List,
  },
  methods: {
    add(){
      var that = this;
      axios
        .post(window.a+"/api/note", {
          'data':that.note,
        })
        .then(function (response) {
          if (response.data.ret == 0) {
            alert("");
          } else if (response.data.ret == 302) {
            alert("");
            that.$router.push("signin");
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  },

  mounted() {
    var that = this;
    axios
      .get(window.a+"/api/note")
      .then(function (value) {
        that.note = value.data.retlist
        console.log(value)
      })
      .catch(function (reason) {
        console.log(reason);
      });
  },





  computed: {
    //list
    year: {
      get() {
        var value2 = new Date(this.value1);
        value2 = value2.getFullYear();
        return value2;
      },
    },
    week: {
      get() {
        //
        var date = new Date(this.value1);
        var date2 = new Date(date.getFullYear(), 0, 1);
        var day1 = date.getDay();
        if (day1 == 0) day1 = 7;
        var day2 = date2.getDay();
        if (day2 == 0) day2 = 7;
        var d = Math.round(
          (date.getTime() -
            date2.getTime() +
            (day2 - day1) * (24 * 60 * 60 * 1000)) /
            86400000
        );
        var date3 = Math.ceil(d / 7);
        if (date.getHours() >= 12) date3 = date3 - 1;
        return date3;
      },
    },
  },
};
</script>

