<template>
  <div id="d1">
    <div v-if="showtime" id="d2" @click.left="ch">
      <p id="p1">{{ hours }}:{{ minutes }}</p>
      <p id="p2">{{ year }}.{{ month }}.{{ date }} {{ week }}</p>
    </div>
    <div id="d3" v-if="showbutton">
      <img src="./assets/home.png" class="dimage3" @click.left="backhome">
      <img src="./assets/refresh.png" class="dimage3" @click.left="refresh">
      <img src="./assets/back.png" class="dimage3" @click.left="back">
    </div>


    <!--.once:-->
    <router-view></router-view>

  </div>
</template>

<script>
  //

  //import Catalogue from "./components/Catalogue.vue"
  //import Sign from "./components/Sign.vue"
  //import docx from "./components/docx.vue"
  export default {
    name: "App",
    components: {},
    data() {
      return {
        //
        year: "2022",
        month: "1",
        date: "1",
        hours: "00",
        minutes: "00",
        seconds: "00",
        week: "",
        //
        showtime: true,
        showbutton: false,
      };
    },

    mounted() {
      //
      if (window.location.href != window.a +"/#/") {
          this.showtime = false
          this.showbutton = true
        } else {
          this.showtime = true
          this.showbutton = false
        }

      //
      setInterval(getTime, 500);
      var that = this;
      function getTime() {
        var dateObj = new Date();
        var year = dateObj.getFullYear(); //
        var month = dateObj.getMonth() + 1; //  (+1)
        var date = dateObj.getDate(); //
        var day = dateObj.getDay();
        var weeks = [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
        ];
        var week = weeks[day]; //day
        var hours = dateObj.getHours(); //
        var minutes = dateObj.getMinutes(); //
        var seconds = dateObj.getSeconds(); //

        if (month < 10) {
          month = "0" + month;
        }
        if (date < 10) {
          date = "0" + date;
        }
        if (hours < 10) {
          hours = "0" + hours;
        }
        if (minutes < 10) {
          minutes = "0" + minutes;
        }
        if (seconds < 10) {
          seconds = "0" + seconds;
        }

        that.year = year;
        that.month = month;
        that.date = date;
        that.hours = hours;
        that.minutes = minutes;
        that.seconds = seconds;
        that.week = week;
      }
    },

    methods: {

      ch() {
        this.$router.push("catalogue");
        //
        if (window.location.href != window.a +"/#/") {
          this.showtime = false
          this.showbutton = true
        } else {
          this.showtime = true
          this.showbutton = false
        }

      },
      backhome() {
        window.location.replace(window.a + "/");
      },
      back() {
        this.$router.back()
        //
        if (window.location.href != window.a +"/#/catalogue") {
          this.showtime = false
          this.showbutton = true
        } else {
          this.showtime = true
          this.showbutton = false
        }
      },

      refresh(){
        this.$router.go(0);
      }

      //

      /* mouseWheel(e) {
         if (e.wheelDelta || e.detail) {
           if (e.wheelDelta > 0 || e.detail < 0) {
            
             //
           }
           if (e.wheelDelta < 0 || e.detail > 0) {
             //
             this.$router.push("catalogue");
             this.showtime = false;
           }
         }
       },*/
    },
  };
</script>

